<template>
    <div id="work">
      <div class="work_visual">
        <img :src="jsonData.view?.viewBigImgSrc" :alt="jsonData.project">
      </div>

      <div class="inner">
        <section class="overview">
          <div class="text_inner">
            <div class="overview_title_box">
              <em class="title">OVERVIEW</em> 
               <h2 v-dompurify-html="jsonData.view?.overviewTitle"></h2>
            </div>
            <div class="overview_text_box">
              <dl v-for="(item, index) in jsonData.view?.websiteInfo" :key="index">
                <dt >{{ item.title }}</dt>
                <dd>{{ item.description }}</dd>
                <!-- <dd>{{ subItem.subText }}</dd> -->
              </dl>
            </div>
          </div>
        </section>
        <section class="mission">
          <div class="text_inner">
              <em class="title">MISSION</em>
              <h3 v-dompurify-html="jsonData.view?.middleTitle"></h3>
              <img :src="jsonData.view?.middleBigImgSrc" alt="">
          </div>
        </section>

        <section class="goals">
          <div class="text_inner">
            <em class="title">PROJECT GOALS</em>
            <ol class="goals_list">
              <li v-for="(subItem, index) in  jsonData.view?.defineList" :key="index">
                <dl>
                  <dt>0{{index+1 }}</dt>
                  <dd v-dompurify-html="subItem.title"></dd>
                  <dd>{{ subItem.description}}</dd>
                </dl>
              </li>
            </ol>
          </div>
    
        </section>
      </div>

      <Pagination
        :current-page="currentPage"
        :total-pages="totalPages"
        :prev-PageData="prevPageData"
        :next-PageData="nextPageData"
        @page-change="changePage"
      />
    </div>
  </template>
    
  <script>
  import { ref,  onMounted, onBeforeUpdate  } from 'vue';
  import { useRoute  } from 'vue-router';
  import { useDataStore } from '@/store/index'
  import { storage } from '@/firebase/init.js';
  import { ref as storageRef, getDownloadURL } from 'firebase/storage';
  import axios from 'axios'
  import Pagination from '@/components/Layouts/common/Pagination.vue'
  
  export default {
    name: 'worksView',
    props: {
    },
    components: { 
      Pagination
    },
    setup(props) {
      const route = useRoute();
      const itemsStore = useDataStore();
      const jsonData = ref({});
      const currentPage = ref( 0 );
      const totalPages = ref( 0 );
      const prevPageData = ref({});
      const nextPageData = ref({});
      let hasFetchedData = false;



      const fetchWorkItems = async () => {  
        if (!hasFetchedData) {
          const storageInstance = storage;
          const jsonFileRef = storageRef(storageInstance, `works.json`);

          try {
              const downloadURL = await getDownloadURL(jsonFileRef);
              const res = await axios.get(downloadURL);
              const workitems = res.data;
  
              const curtPage = workitems[route.params.id];
              itemsStore.workItemsObj(workitems);
              totalPages.value = workitems.length;
              jsonData.value = workitems[route.params.id];
              nextPageData.value = workitems[route.params.id];
              currentViewPage(curtPage.id-1);
              currentPage.value = workitems[route.params.id].id - 1;
              hasFetchedData = true; 

          } catch (error) {
              console.error("Error fetching data:", error);
          }
        }
     
      };

      const changePage = ( page ) => {
        currentPage.value = page;
      }
      
      const currentViewPage = (curtPage) =>{
        currentPage.value = curtPage;
      }
      onBeforeUpdate( () =>{
        fetchWorkItems();
      })
      onMounted(() => {
        fetchWorkItems();
        currentViewPage();
      });
      onMounted( ()=>{
        hasFetchedData = false;
      })

      return {
        jsonData,
        routeParamsId:  Number(route.params.id),
        currentPage,
        totalPages,
        prevPageData,
        nextPageData,
        itemsStore,
        changePage,
       
      };
    },
  }
  </script>
    
  <style lang="scss" scoped>
  @import "@/assets/scss/__variables.scss";
  #work {
    .work_visual{
      >img{ width: 100%; }
    }
    .inner{
      display: block;
      .text_inner{
        display: flex;
        padding:vw( 100px);
        justify-content: space-between;
        align-items:center;
        .overview_title_box{
          width: 55%;
          .title{ 
            display:block;
            font-size:vw(26px);
            margin-bottom: vw(10px);
            color: $dpColor;
            font-weight: 600;
          }
          h2{
            font-size:vw(40px);
            font-family: 'Noto Sans Korean';
            font-weight: bold;
            line-height: 1.35;
          }
        }
        .overview_text_box{
          width: 45%;
          dl{
            dt{
              margin-bottom: vw(16px);
              font-size:vw(24px);
              font-family: 'Noto Sans Korean';
              font-weight: bold;
            }
            dd{
              margin-bottom: vw(24px);
              font-size:vw(22px);
              font-family: 'Noto Sans Korean';
              line-height: 1.35;
            }
          }
        }
      }
      .mission{
        display: block;
        .text_inner{
          display: block;
          position: relative;
          .title{ 
            display:block;
            font-size:vw(26px);
            padding-top: vw(120px);
            margin-bottom: vw(30px);
            color: $dpColor;
            font-weight: 600;
          }
          h3{
            display: inline-block;
            position: relative;
            font-size:vw(50px);
            font-family: 'Noto Sans Korean';
            font-weight: bold;
            line-height: 1.2;
            z-index: 2;
            &:after{
              content: '';
              display: block;
              position: absolute;
              bottom:0;
              left:-15%; 
              width: 100%;
              height: 72%;
              background: #fff;
              z-index: -1;
            }
          }
          img{
            position: absolute;
            top:0;
            left:0;
            width: 100%;
          }
        }
      }
      .goals{
        flex-direction:row;
        justify-content: space-between;
        .text_inner{
          padding:vw( 100px);
          align-items: start;
          .title{
            display:block;
            font-size:vw(26px);
            color: $dpColor;
            font-weight: 600;
          }
          .goals_list{
            display: flex;
            flex: 1;
            margin-left: vw(100px);
            li{
              dl{
             
                dt{
                  margin-right: vw(18px);
                  float: left; 
                  font-size:vw(24px);
                  font-weight: bold;
                  color: $dpColor;
                  height: vw(150px);
                  &+dd{
                    margin-top:vw(-8px);
                    font-size:vw(30px);
                    font-weight: bold;
                  }
                }
                dd{
                  margin-top: vw(14px);
                  float: left;
                  width: 80%;
                  font-size: vw(18px);
                  line-height: 1.35;
                  font-family: 'Noto Sans Korean';
                }
                &:after{
                  content: '';
                  display: block;
                  clear: both;
                }
              }
            }
          }

        }
      }
    }
   
  }
  </style> 
    